import React, { useContext, useEffect, useState } from 'react';
import ReactPlayer from 'react-player';
import SpeedDial from '@mui/material/SpeedDial';
import MenuIcon from "@mui/icons-material/Menu";
import SpeedDialAction from '@mui/material/SpeedDialAction';
import {
  PlayArrow as PlayArrowIcon,
  Pause as PauseIcon,  
  SkipNext as SkipNextIcon,
  SkipPrevious as SkipPreviousIcon,
  VolumeOff as VolumeOffIcon,
  VolumeUp as VolumeUpIcon,
  Fullscreen as FullscreenIcon,
} from '@mui/icons-material';
import { TVChannelsContext } from "./TVChannels";

interface PlayerwSpeedialsProps {
  url: string;
  topMenu: Boolean;
  userInactive: Boolean;
}

const PlayerwSpeedials: React.FC<PlayerwSpeedialsProps> = ({ url,topMenu,userInactive }) => {
  const context = useContext(TVChannelsContext);  
  if (!context) {
    throw new Error("ChannelOverlay must be used within a TVChannels provider");
  }
  const { channels,switchChannelById } = context;
  const [open, setOpen] = useState(false);
  const [playing, setPlaying] = useState(true);
  const [muted, setMuted] = useState(false);
  const [isFullscreen, setIsFullscreen] = React.useState<boolean>(false);

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const switchNextChannel = () => {    
    const next=channels.getNextChannel();
    if (next) switchChannelById(next.id);
  };
  const switchPreviousChannel = () => {    
    const next=channels.getPreviousChannel();
    if (next) switchChannelById(next.id);
  };
  const handleFullscreenToggle = () => {
    if (!document.fullscreenElement) {
      if (document.documentElement.requestFullscreen) {
        document.documentElement.requestFullscreen().then(() => {
          setIsFullscreen(true);
        });
      }
    } else {
      if (document.exitFullscreen) {
        document.exitFullscreen().then(() => {
          setIsFullscreen(false);
        });
      }
    }
  };
  const actions = [
    {
      icon: playing ? <PauseIcon /> : <PlayArrowIcon />,
      name: playing ? 'Pause' : 'Play',
      action: () => setPlaying(!playing),
    },
    { icon: <SkipNextIcon />, name: 'Next', action: () => switchNextChannel() },
    { icon: <SkipPreviousIcon />, name: 'Previous', action: () => switchPreviousChannel() },
    {
      icon: muted ? <VolumeOffIcon /> : <VolumeUpIcon />,
      name: muted ? 'Unmute' : 'Mute',
      action: () => setMuted(!muted),
    },
    { icon: <FullscreenIcon />, name: 'Full Screen', action: () => handleFullscreenToggle() },
  ];
  useEffect(() => {
    const onFullScreenChange = () => {
      setIsFullscreen(!!document.fullscreenElement);
    };

    document.addEventListener("fullscreenchange", onFullScreenChange);

    return () => {
      document.removeEventListener("fullscreenchange", onFullScreenChange);
    };
  }, []);
  return (
    <>
      <ReactPlayer
        url={url}
        playing={playing}
        muted={muted}
        width="100%"
        height="100%"
      />
      <SpeedDial
        ariaLabel="Player Controls"
        FabProps={{size:"small",color:"default",variant:"extended",sx:{}}}
        icon={<MenuIcon />}
        onClose={handleClose}
        onOpen={handleOpen}
        open={open}
        direction="left"
        sx={{ position: 'fixed', bottom: 1, right:"0.91rem", ...( (topMenu || userInactive) && { display: "none" } ) }}
      >
        {actions.map((action) => (
          <SpeedDialAction
            key={action.name}
            icon={action.icon}
            tooltipTitle={action.name}
            onClick={() => {
              action.action();
              handleClose();
            }}
          />
        ))}
      </SpeedDial>
    </>
  );
}

export default PlayerwSpeedials;